const Tuote=({tuote, addCart, removeCart}) => {
    return (
        <div className="tuote">
        <p>{tuote.nimi}</p>
        <p>{tuote.hinta + "€"}</p>
        <img src = {tuote.kuva} alt="kuva"/>
        <br></br>
        <button onClick={()=>removeCart(tuote.id)}> - </button>
        <span id="testi">{tuote.maara}</span>
        <button onClick={()=>addCart(tuote.id)}> + </button>
    </div>
    )
}
const Tuotteet2=({tuotteet, setTuotteet}) => {
    const addCart = id => {
        setTuotteet(tuotteet.map(tuote => {
            if (id === tuote.id) {
                return {...tuote, maara: tuote.maara + 1};
            } else 
            return tuote;
        }))
    }
    const removeCart = id => {
        setTuotteet(tuotteet.map(tuote => {
            if (id === tuote.id && tuote.maara > 0) {
                return {...tuote, maara: tuote.maara - 1};
            } else 
            return tuote;
        }))
    }
    return (
        <div className="tuotteet">
             {tuotteet.map(s=> <Tuote key = {s.id} tuote = {s} addCart={addCart} removeCart={removeCart}/>)}
        </div>
    );
}
export default Tuotteet2;