import { useState } from "react";

const Vahvistus = ({ tuote }) => {
    return (
        <div class="container">
            <div class="row">
                <div className="tuotteet">
                    <div className="tuotteet2">
                        <p>{tuote.nimi}</p>
                        <p>{tuote.hinta}</p>
                        <img alt="kuva" className="tuote" src={tuote.kuva} /><br/>
                    </div>
                </div>
                <br/>
            </div>
        </div>
    )
}
const Vahvistus2 = ({tuotteet, setTuotteet}) => {
    const [page, setPage] = useState(0);
    const [newOstos, setNewOstos] = useState({Nimi: "",  Email: "", Osoite: "", Postinumero: "", Postitoimipaikka: ""});

    const ostosHandler = (e, field) => {
        const tempOstos = { ...newOstos };
        tempOstos[field] = e.target.value;
        setNewOstos(tempOstos);
    }

    const calcTotal = () => {
        const maksu = tuotteet.map(l => l.hinta * l.maara);
        return maksu.reduce((a, b) => Math.round((a + b + Number.EPSILON) * 100) / 100)
    }
    const filteredTuotteet = tuotteet.filter(c => c.maara > 0);

    const setToZeros = () => {
        const tempTuoteet = tuotteet.map(tuot => {
            tuot = { ...tuot, maara: 0 };
            return tuot;
        });
        setTuotteet(tempTuoteet);
    }

    return (
        <div>
            {page === 0 &&
                <div>
                    {filteredTuotteet.map(c => <Vahvistus key={c.id} tuote={c} />)}
                    <p><b>hinta:</b> {calcTotal()}€</p>
                    <button onClick={() => setPage(page + 1)}>Vahvista osto</button>
                </div>
            }{page === 1 &&
                <div>
                    <form>
                        <h2>Vahvistus</h2><br/>
                        <label for="fname">First name:</label>
                        <input type="text" name="fname" placeholder="Nimi" value={newOstos.Nimi}  onChange={e => ostosHandler(e, "Nimi")} required/><br/>
                        <label for="email">E-mail:</label>
                        <input type="mail" name="Email" placeholder="Email" value={newOstos.Email} onChange={e => ostosHandler(e, "Email")} required/><br/>
                        <label for="katupost">katuosoite:</label>
                        <input type="text" name="katupost" placeholder="Osoite" value={newOstos.Osoite} onChange={e => ostosHandler(e, "Osoite")} required/><br/>
                        <label for="Postitoimipaikka">postitoimipaikka:</label>
                        <input type="text" name="Postitoimipaikka" placeholder="Postitoimipaikka" value={newOstos.Postitoimipaikka} onChange={e => ostosHandler(e, "Postitoimipaikka")} required/><br/>
                        <label for="Postinumero">Postinumero:</label>
                        <input type="number" name="Postinumero" placeholder="Postinumero" value={newOstos.Postinumero} onChange={e => ostosHandler(e, "Postinumero")} required/><br/>

                        <input type="submit" value="Submit" onClick={() => setPage(page + 1) + setToZeros()}/>
                    </form>
                </div>
            }
            {page === 2 &&
                <div>
                    <h2>Ostos on vahvistettu</h2>
                </div>
            }
        </div>
    )
}

export {Vahvistus2}