import { useState } from 'react';

const Shopping = ({ tuote }) => {
    const [showCart, setShowCart] = useState(true);

    return (
        <div className="tuotteet">
            <p onClick={() => setShowCart(!showCart)}></p>
            {showCart &&
            <div>
                <p>{tuote.nimi}: {tuote.maara}kpl</p>
            </div>    
            }
        </div>
    )
}

const ShopCart = ({tuotteet, setTuotteet }) => {
    const calcTotal = () => {
        const maksu = tuotteet.map(l => l.hinta * l.maara);
        return maksu.reduce((a, b) => Math.round((a + b + Number.EPSILON) * 100) / 100)
    }
    const setToZero = () => {
        const tempTuotteet = tuotteet.map(tuot => {
            tuot = { ...tuot, maara: 0};
            return tuot;
        });
        setTuotteet(tempTuotteet);
    }

    const filteredTuotteet = tuotteet.filter(c => c.maara > 0);
    return (
        <div class="cart">
            <button onClick={() => setToZero()}>Tyhjennä</button>
            {filteredTuotteet.map(c => <Shopping key={c.id} tuote={c} />)}<br />
            <p className="hinta">Hinta: {calcTotal()}€</p>
        </div>
    )
}

export { ShopCart };