// import logo from './logo.svg';
import './App.css';
import React, { useState } from 'react';
import tuotteetdata from './components/Tuotteet-data.js';
import Tuotteet2 from './components/Tuotteet.js';
import { ShopCart } from './components/Cart.js';
import { Vahvistus2 } from './components/Vahvistus';



const App=() => {
  const [showCart, setShowCart] = useState(false);
  const [show] = useState(true);
  const [tuotteet, setTuotteet] = useState(tuotteetdata);
  const [page, setPage] = useState(0);

  return (
    <div className="App">
      <header className="App-header">
      <div>
      <h1>Fanikauppa</h1>
      <img onClick={()=>setShowCart(!showCart)} id="cart" src="https://www.freeiconspng.com/thumbs/cart-icon/basket-cart-icon-27.png"></img>
      </div>
      </header>
      
      {showCart &&
      <div id="cart">
        <button onClick={() => setPage(page + 1)}>Vahvista</button>
        <ShopCart tuotteet={tuotteet} setTuotteet={setTuotteet}/>
      </div>}
      {page == 0 &&
      <div id="tuotteet">
      {show && <Tuotteet2 tuotteet = {tuotteet} setTuotteet = {setTuotteet} />}
      </div>
      }{page == 1 &&
        <div>
              <Vahvistus2 tuotteet={tuotteet} setTuotteet={setTuotteet} />
              <button onClick={() => setPage(page - 1)}>Etusivu</button>
            </div>
      }
    </div>
  );
}

export default App;
